import "../styles/usage/page/app.scss";
import commonUtil from "./utils/common.util.js";

// 自定义 scroll 指令
commonUtil.dirScroll();
// views
import mMainHot from './components/m-main-hot.vue';
import mMainAttention from './components/m-main-attention.vue';
import mMain from './components/m-main.vue';
import index from "./components/index.vue";
import my from "./components/my.vue";
import main from "./components/main.vue";
import community from "./components/community.vue";
import shopping from "./components/shopping.vue";
import login from "./components/login.vue";
import register from "./components/register.vue";
import find from "./components/find.vue";
import block from "./components/block.vue";
import Hot from "./components/Hot.vue";
import list from "./components/list.vue";
import swiper from "./components/swiper.vue";
import search from './components/search.vue';
// router
// import Vue from "./libs/vue.js";
// import VueRouter from "./libs/vue-router.js";
// Vue.use(VueRouter);

//mint-ui

// 在根组件加入 store，让它的子组件和 store 连接
import store from './vuex/store';

let App = Vue.extend({
    store: store
});

let router = new VueRouter();

router.map({
  '/': {
    component: index,
    subRoutes: {
      '/': {
        component: mMain,
        subRoutes:{
            '/':{
                component:mMainHot,
                subRoutes:{
                    '/':{
                        component:mMainAttention
                    }
                }
            }
        }
      },
      '/my': {
        component: my
      },
      '/community': {
        component: community
      },
      '/shopping': {
        component: shopping
      }
    }
},
'/mMain':{
        component:mMain
    },
 '/login': {
     component: login,
 },
 '/register': {
   component: register
 },
 '/find': {
   component: find
 },
  '/search':{
    component:search,
  }
});

router.start(App, 'body');



